/* Sign In Page Styling */
.signin-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 90vh;
  background-color: #f4f7fc;
}

.signin-card {
  width: 100%;
  max-width: 400px;
  padding: 16px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.mat-card-header {
  background-color: #fff;
}

h1 {
  font-size: 1.8rem;
  color: #343a40;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 16px;
}

.full-width {
  width: 100%;
}

/* Button Styles */
button.full-width {
  width: 100%;
}

/* Error Message Styling */
.error-message {
  color: #d9534f;
  font-size: 0.9rem;
  margin-top: 8px;
  text-align: center;
}

/* Styling for the Forgot Password & New User buttons */
.button-group {
  display: flex;
  justify-content: space-between;
}

.button-group button {
  width: 48%;
}

/* Responsive Design for Small Screens */
@media (max-width: 600px) {
  .signin-card {
    width: 90%;
    padding: 20px;
  }

  h1 {
    font-size: 1.5rem;
  }
}
